.main {
  &__container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: auto;
    width: (4 * $card-widht) + (3 * 48px) + (2 * $horizontal-padding);
    padding: $vertical-padding $horizontal-padding;
  }

  &__card {
    margin-right: $card-margin-right;
    margin-bottom: $card-margin-top;
  }
}
